@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: auto;
  overflow: auto;
  min-height: 300px;
  padding: 30px;
  border-radius: 6px;
  /*background-image: url("images/mountains-dark.png");*/
  background: rgba(136, 136, 136, 0.3); 
  background-size: 90%;
}

/**The whole week box area**/
.week{
  margin: 500px auto 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  min-height: 300px;
  /*background-color: #465c0b;*/
  background: rgba(136, 136, 136, 0.3); 
  border-radius: 5px;

}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 20px;
  color: white;
  font-size: 2em;
}

body {
  font-family: 'Poppins', sans-serif;
  background-image: url(/static/media/mountains.109625a0.jpg);
  background-size: 100%;
}

/**The actual weekday box itself**/
.weekday {
  color: white;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
  min-height: 300px;
  width: 140px;
  background-color: grey;
  background: rgba(245, 245, 245, 0.3); 
  -webkit-align-items: center; 
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 5px;
}

.weekday h3, p {
  -webkit-justify-content: center;
          justify-content: center;
}

button {
  color: pink;
}
