/* this code was pulled from 
https://www.w3schools.com/howto/howto_css_loader.asp */
.Conditions_Loader__3UU6P {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid rgb(90, 89, 89); /* Blue */
    border-radius: 50%;
    width: 50px;
    height: 50px;
    -webkit-animation: Conditions_spin__3w0nI 2s linear infinite;
            animation: Conditions_spin__3w0nI 2s linear infinite;
    margin: 10px auto;
}

@-webkit-keyframes Conditions_spin__3w0nI {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Conditions_spin__3w0nI {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
} 
.Forecast_textInput__39zH8 {
    font-size: large;
    border: 1px solid #EEEEEE;
