}

.Forecast_textInput__39zH8:focus {
    outline: none;
    border-color: #719ECE;
    box-shadow: 0 0 10px rgb(50, 100, 153);
}

.Forecast_Radio__1Ejlt {
    font-size: small;
}

.Forecast_Button__3Y_IL {
    display: block;
    margin: 10px auto;
    padding: .5rem;
    font-size: 1em;
    background: darkcyan;
    border-radius: 5px;
    color: white;
    cursor: pointer;
} 
